@import "../../../style/theme/default/var.css";
.member-property{
  &.el-collapse{
    border: 0;
    & .el-collapse-item{
      border:1px solid var(--theme-border-color-primary);
      border-radius: var(--border-radius-base);
      margin-bottom: var(--theme-margin-base);
      padding:0 var(--theme-padding-base);
      &__header{
        border:0;
        padding:0;
        height:40px;
        line-height: 40px;
        background-color: transparent;
        & .el-badge{
          float: right;
          margin-right: var(--theme-margin-small);
        }
        &__arrow{
          color: var(--theme-background-color-secondary);
          height: 40px;
          line-height: 40px;
        }
      }
      &__wrap{
        background-color: transparent;
        width:100%;
        border-bottom:0;
      }
      &__content{
        padding:var(--theme-padding-base) 0;
        font-size: var(--theme-font-size-small);
      }
      &:hover{
        border-color: var(--theme-color-primary);
      }
      &.is-active{
        background-color: var(--theme-background-color-primary);
        & .el-collapse-item__header{
          border-bottom:1px solid var(--theme-border-color-primary);
        }
      }
    }
  }
  & .el-badge {
    line-height:1;
    & .el-badge__content{
      margin-top: 10px;
    }
  }
  &-list{
    a{
      color:var(--color-black);
      cursor: pointer;
      margin-right:var(--theme-margin-base);
      margin-bottom:var(--theme-margin-small);
      display: inline-block;
      &:hover,
      &.is-selected{
        color:var(--theme-color-primary);
      }
      &.is-disabled {
        color:var(--theme-font-color-secondary);
        cursor: default;
      }
    }
  }
}

@component-namespace member{
  @b choose {
      &.el-collapse{
        border: 0;
      & .el-collapse-item{
          border:1px solid var(--theme-border-color-primary);
          margin-bottom: var(--theme-margin-base);
      &__header{
         border:0;
         background-color: var(--theme-background-color-primary);
       }
      &__wrap{
         background-color: transparent;
         width:100%;
         border-bottom:0;
       }
      &:hover{
         /* border-color: var(--theme-color-primary);*/
       }
        @when focus{
          border-color: var(--theme-color-primary);
        }
      &.is-active{
       }
      }
    }

    @e delete{
       color:var(--theme-color-primary);
       cursor: pointer;
     }
  }
}



